.work__filters {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 1.75rem;
    margin-bottom: var(--mb-2);
}

.work__item {
    cursor: pointer;
    color: var(--title-color);
    padding: 0.25rem 0.75rem;
    font-weight: var(--font-medium);
    border-radius: 0.5rem;
    text-transform: capitalize;
}

.work__item:hover {
    background-color: var(--title-color);
    color: var(--container-color);
}

.work__container {
    grid-template-columns: repeat(3, 270px);
    column-gap: 1.8rem;
    justify-content: center;
}

.work_content {
    position: relative;
    border: 1px solid rgba(0,0,0,0.1);
    background-color: var(--container-color);
    padding: 1.25rem;
    border-radius: 1rem;
}

.work__img {
    width: 295px;
    height: 200px;
    border-radius: 1rem;
    margin-bottom: var(--mb-1);
}

.work__title {
    font-size: var(--normal-font-size);
    margin-bottom: var(--mb-0-5);
    font-weight: var(--font-medium);
}

.work__button {
    color: var(--text-color);
    font-size: var(--small-font-size);
    display: flex;
    align-items: center;
    column-gap: 0.25rem;
}

.work__button-icon {
    font-size: 1rem;
    transition: 0.4s;
    cursor: pointer;
}

.work__button:hover .work__button-icon {
    transform: translateX(0.25rem);
}

/* Active work */
.active-work {
    background-color: var(--title-color);
    color: var(--container-color);
}
/*project details*/
.work__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}

.work__modal-content {
    width: 500px;
    position: relative;
    background-color: var(--container-color);
    padding: 3rem 2rem 2rem;
    border-radius: 1.5rem;
}

.work__modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
}

.work__modal-title {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1);
}

.work__modal-descption{
    text-align: justify;
    font-size: var(--small-font-size);
    padding: 0 3.5rem;
    margin-bottom: var(--mb-2);
}

.work__modal-details {
    row-gap: 0.75rem;
}

.work__modal-detail{
   display: flex; 
   align-items: center;
   column-gap: .5rem;
   padding: 0 3.5rem;
   margin-bottom: var(--mb-1);
}

.work__modal-icon {
    color: var(--title-color);
    font-size: 1.1rem;
}

.work__modal-info {
    font-size: var(--small-font-size);
}

.active-modal {
    opacity: 1;
    visibility: visible;
}

/*============ BREAKPOINTS =============*/
/* for large devices */
@media screen and (max-width: 992px){
    .work__filters {
        font-size: var(--normal-font-size);
    }
    .work__container {
        grid-template-columns: repeat(3, 218px);
        gap: 1.25rem;
    }

    .work__container {
        padding: 1rem;
    }

    .work__img {
        margin-bottom: 0.75rem;
    }

    .work__title {
        margin-bottom: 0.25rem;
    }

  }
  
  /* for medium devices */
  @media screen and (max-width: 768px){
    .work__container {
        grid-template-columns: repeat(2, 1fr);
    }

    .work_content{
        padding: 3.5rem 0.5rem 1.25rem 1.5rem;
    }
  }
  
  @media screen and (max-width: 576px){ 
    .work__container {
        grid-template-columns: 1fr;
    }
    
    .work__img {
        width: 100%;
    }

    .work_content {
        padding: 4.5rem 1.5rem 2.5rem;
    }

    .work__modal-descption{
        padding: 0;
    }
  }
  
  /* for small devices */
  @media screen and (max-width: 350px){
    .work__container {
        grid-template-columns: max-content;
    }

    .work__item {
        font-size: var(--small-font-size);
    }

    .work__filters {
        column-gap: 0.25rem;
    }
  }