.misc__container {
    grid-template-columns: repeat(2, 450px);
    column-gap: 3rem;
    justify-content: center;
}

.misc__relevent__content {
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1rem 2rem;
    border-radius: 1.25rem;
}

.misc__relevent__title {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    text-align: center;
    margin-bottom: var(--mb-1-5);
}

.misc__relevent__box {
    display: grid;
    justify-content: center;
    column-gap: 2.5rem;
}

.misc__relevent__data {
    padding-bottom: 0.5rem;
    display: flex;
    column-gap: 0.5rem;
}

.misc__relevent__data h3{
    display: inline;
    padding-right: 1rem;
}

.misc .bx-badge-check {
    font-size: 1rem;
    color: var(--title-color);
}

.misc__relevent__name {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    line-height: 18px;
}

.misc__relevent__date {
    font-size: var(--tiny-font-size);
}

.misc__notrelevent__box {
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

.misc__notrelevent__group {
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;
}

.misc__notrelevent__data{
    display: inline;
    padding: 1rem;
    text-align: center;
}

.irrelevent__img {
    width: 100px;
    border-radius: 0.5rem;
    justify-self: center;
}

.misc__notrelevent__box {
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

/*============ BREAKPOINTS =============*/
/* for large devices */
@media screen and (max-width: 992px){
    .misc__container {
        grid-template-columns: max-content;
        row-gap: 2rem;
    }
  }
  
  /* for medium devices */
  @media screen and (max-width: 576px){ 
    .misc__container {
        grid-template-columns: 1fr;
    }

    .misc__relevent__content {
        padding: 1.5rem;
    }
  }
  
  /* for small devices */
  @media screen and (max-width: 350px){
    .misc__notrelevent__box,
    .misc__relevent__box {
        column-gap: 1.25rem;
    }

    .misc__relevent__name {
        font-size: var(--small-font-size);
    }
  }